const inputName = document.querySelector('.hero-section-form__input-name');

function isValidName() {
  // 1. Между буквами или цифрами допускается только один пробел.
  // 2. Допустимы буквы латинского и кириллического алфавитов, а также цифры.
  // 3. Имя не может состоять только из цифр.
  // 4. Допустимо ввести не более трех слов, разделенных пробелами.
  const nameRegex =
    /^(?!^\s)(?!.*\s$)(?=(.*[a-zA-Zа-яА-ЯёЁіІїЇєЄґҐ]){1,})(?=(.*[a-zA-Zа-яА-ЯёЁіІїЇєЄґҐ0-9]){3,})[a-zA-Zа-яА-ЯёЁіІїЇєЄґҐ0-9]+(?:\s[a-zA-Zа-яА-ЯёЁіІїЇєЄґҐ0-9]+)?(?:\s[a-zA-Zа-яА-ЯёЁіІїЇєЄґҐ0-9]+)?$/;

  if (!nameRegex.test(inputName.value.trim())) {
    return false;
  }

  return true;
}

// обработчик события focus для input
inputName.addEventListener('focus', function () {
  if (!isValidName()) {
    // Если номер не валиден, добавляем класс активен и убираем класс успеха
    inputName.classList.remove('success-border');
    inputName.classList.add('active-border');
  } else {
    // Если номер валиден, добавляем класс успеха и убираем класс активен
    inputName.classList.remove('active-border');
    inputName.classList.add('success-border');
  }
});

//обработчик события blur для input
inputName.addEventListener('blur', function () {
  if (inputName.value.trim().length === 0) {
    // Если длина текста меньше, 1 убираем все классы
    inputName.classList.remove(
      'active-border',
      'error-border',
      'success-border'
    );
  } else {
    if (!isValidName()) {
      // Если номер не валиден, добавляем класс активен и убираем класс успеха
      inputName.classList.remove('success-border');
      inputName.classList.add('active-border');
    } else {
      // Если номер валиден, добавляем класс успеха и убираем класс активен
      inputName.classList.remove('active-border');
      inputName.classList.add('success-border');
    }
  }
});

// Обработчик события ввода в поле номера телефона input
inputName.addEventListener('input', function () {
  if (inputName.value.trim().length === 0) {
    // Если длина текста меньше, 1 убираем все классы кроме активен
    inputName.classList.remove('error-border', 'success-border');
    inputName.classList.add('active-border');
  } else {
    if (!isValidName()) {
      // Если номер не валиден, добавляем класс ошибки и убираем класс успеха
      inputName.classList.remove('success-border', 'error-border');
      inputName.classList.add('active-border');
    } else {
      // Если номер валиден, добавляем класс успеха и убираем класс ошибки
      inputName.classList.remove('active-border', 'error-border');
      inputName.classList.add('success-border');
    }
  }
});

export { isValidName };
