import imageBullDesktopBase from '../../images/index/image-bull-desktop-base.png';
import imageBullMobileBase from '../../images/index/image-bull-mobile-base.png';

import imageBullDesktopX2 from '../../images/index/image-bull-desktop@2.png';
import imageBullMobileX2 from '../../images/index/image-bull-mobile@2.png';

import imageBullDesktopX3 from '../../images/index/image-bull-desktop@3.png';
import imageBullMobileX3 from '../../images/index/image-bull-mobile@3.png';


const imageWrapper = document.querySelector('.results-chart__image-wrapper');

let screenWidth = window.innerWidth;

const scaleTarget = () => (screenWidth = window.innerWidth);

scaleTarget();

window.addEventListener('resize', scaleTarget);

const uploadedImage = () => {
    if (screenWidth <= 1169) {
        return imageBullMobileX2;
    }
    if (screenWidth >= 1170) {
        return imageBullDesktopX2;
    }
}

const baseImage = () => {
  if (screenWidth <= 1169) {
      return imageBullMobileBase;
  }
  if (screenWidth >= 1170) {
      return imageBullDesktopBase;
  }
};

function createImageMarcup() {
    const listCards = `
        <picture>
            <source media="(min-width: 1170px)" data-srcset="${imageBullDesktopX2} 1x, ${imageBullDesktopX2} 2x, ${imageBullDesktopX3} 3x"/>
            <source media="(max-width: 1169px)" data-srcset="${imageBullMobileX2} 1x, ${imageBullMobileX2} 2x, ${imageBullMobileX3} 3x"/>
            <img alt="image-bull" class="lazy" src="${baseImage()}" data-src="${uploadedImage()}"/>
        </picture>
    `

    return listCards
}

imageWrapper.innerHTML = createImageMarcup();
