import { isValidPhoneNumber } from './intl-tel-input';
import { isValidName } from './name-input';

const requestForm = document.querySelector('.hero-section-form');
const inputTel = document.querySelector('.hero-section-form__input-tel');
const inputName = document.querySelector('.hero-section-form__input-name');

requestForm.addEventListener('submit', handleSubmit);

function handleSubmit(event) {
  event.preventDefault();
  const form = event.target;

  const tel = form.elements.tel.value;
  const name = form.elements.name.value.trim();

  if ((isValidPhoneNumber() === false) & (isValidName() === false)) {
    inputTel.classList.remove('active-border', 'success-border');
    inputTel.classList.add('error-border');

    inputName.classList.remove('active-border', 'success-border');
    inputName.classList.add('error-border');

    return console.log('Please fill in all the fields!');
  }

  if (isValidPhoneNumber() === false) {
    inputTel.classList.remove('active-border', 'success-border');
    inputTel.classList.add('error-border');

    return console.log('Please fill in all the fields!');
  }

  if (isValidName() === false) {
    inputName.classList.remove('active-border', 'success-border');
    inputName.classList.add('error-border');

    return console.log('Please fill in all the fields!');
  }

  form.reset();
  inputTel.classList.remove('active-border', 'error-border', 'success-border');
  inputName.classList.remove('active-border', 'error-border', 'success-border');

  console.log(`tel: ${tel}, name: ${name}`);
}
