import 'intl-tel-input/build/css/intlTelInput.css';
import intlTelInput from 'intl-tel-input';
import utils from 'intl-tel-input/build/js/utils';
import $ from 'jquery';
import 'inputmask/dist/jquery.inputmask';

const inputTel = document.querySelector('.hero-section-form__input-tel');

// Инициализация intl-tel-input
const iti = intlTelInput(inputTel, {
  utilsScript: utils,
  initialCountry: 'auto',
  geoIpLookup: function (callback) {
    fetch('https://ipapi.co/json')
      .then(function (res) {
        return res.json();
      })
      .then(function (data) {
        callback(data.country_code);
      })
      .catch(function () {
        callback('us');
      });
  },
  nationalMode: false,
  onlyCountries: ['ua', 'ru', 'us'], // Ограничение доступных стран
});

// Обновление маски при инициализации
const countryData = iti.getSelectedCountryData();
const countryCode = countryData.iso2;
$(inputTel).inputmask({
  mask: getPhoneMaskByCountryCode(countryCode),
});

// Обработчик события изменения страны
$(inputTel).on('countrychange', function (e) {
  const countryData = iti.getSelectedCountryData();
  const countryCode = countryData.iso2;

  // Очистить значение инпута
  $(inputTel).val('');

  // Обновление маски в зависимости от выбранной страны
  $(inputTel).inputmask({
    mask: getPhoneMaskByCountryCode(countryCode),
  });
});

// Функция получения маски номера телефона по коду страны
function getPhoneMaskByCountryCode(countryCode) {
  if (countryCode === 'ua') {
    inputTel.setAttribute('placeholder', '+380 __ ___ ____');
    return '+380 99 999 9999'; // Маска для Украины
  }

  if (countryCode === 'ru') {
    inputTel.setAttribute('placeholder', '+7 (___) ___ __ __');
    return '+7 (999) 999 99 99'; // Маска для России
  }

  if (countryCode === 'us') {
    inputTel.setAttribute('placeholder', '+1 (___) ___ ____');
    return '+1 (999) 999 9999'; // Маска для США
  }

  inputTel.setAttribute('placeholder', '+99 ___ ___ ____');
  return '+99 999 999 9999';
}

//весь код ниже не обязателен для нужен только для декоративной части поля инпут

function isValidPhoneNumber() {
  return iti.isValidNumber();
}

function selectedCountryData() {
  return iti.getSelectedCountryData();
}

// обработчик события focus для input
inputTel.addEventListener('focus', function () {
  if (!isValidPhoneNumber()) {
    // Если номер не валиден, добавляем класс ошибки и убираем класс успеха
    inputTel.classList.remove('success-border');
    inputTel.classList.add('active-border');
  } else {
    // Если номер валиден, добавляем класс успеха и убираем класс ошибки
    inputTel.classList.remove('active-border');
    inputTel.classList.add('success-border');
  }
});

//обработчик события blur для input
inputTel.addEventListener('blur', function () {
  if (inputTel.value.trim().length < selectedCountryData().dialCode.length) {
    // Если длина текста меньше, чем максимальная длина кода страны, убираем все классы
    inputTel.classList.remove(
      'active-border',
      'error-border',
      'success-border'
    );
  } else {
    if (!isValidPhoneNumber()) {
      // Если номер не валиден, добавляем класс ошибки и убираем класс успеха
      inputTel.classList.remove('success-border');
      inputTel.classList.add('active-border');
    } else {
      // Если номер валиден, добавляем класс успеха и убираем класс ошибки
      inputTel.classList.remove('active-border');
      inputTel.classList.add('success-border');
    }
  }
});

// Обработчик события ввода в поле номера телефона input
$(inputTel).on('input', function () {
  if (inputTel.value.trim().length < selectedCountryData().dialCode.length) {
    // Если длина текста меньше, чем максимальная длина кода страны, убираем все классы
    inputTel.classList.remove('error-border', 'success-border');
    inputTel.classList.add('active-border');
  } else {
    if (!isValidPhoneNumber()) {
      // Если номер не валиден, добавляем класс ошибки и убираем класс успеха
      inputTel.classList.remove('success-border', 'error-border');
      inputTel.classList.add('active-border');
    } else {
      // Если номер валиден, добавляем класс успеха и убираем класс ошибки
      inputTel.classList.remove('active-border', 'error-border');
      inputTel.classList.add('success-border');
    }
  }
});

export { isValidPhoneNumber };
